@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap");

h1 {
	font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-weight: 500;
	font-size: 4rem;
	color: var(--darkBlue);
	margin-top: 3rem;
	margin-bottom: 3rem;
	text-align: center;
}

@media screen and (max-width: 768px) {
	h1 {
		line-height: 4rem;
	}
}

h2 {
	font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-weight: 500;
	font-size: 2rem;
	color: var(--darkBlue);
	/* margin-top: 0; */
	/* margin-bottom: 1rem; */
}

h2.footer {
	background-color: var(--midBlue);
	margin: 0;
	margin-bottom: 1rem;
	padding: 2rem;
	text-align: center;
	width: 50%;
}

h2.giftcard {
	padding: 0.5rem;
	padding-bottom: 0;
}

h3 {
	font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-weight: 500;
	font-size: calc(12px + 1.5vw);
	color: var(--darkBlue);
	padding-bottom: 0.5rem;
	margin: 0;
}

input {
    font-family: 'Oswald', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size:calc(12px + 1.5vw); 
    color: rgb(175, 175, 175);
    font-style: italic;
    background-color: var(--offWhite);
    height: 3.5rem;
    overflow: auto;
    width: 100%;
    box-sizing: border-box;
    border: none;
    margin: 0;
    padding-left: 0.5rem;

}

input::placeholder {
	color: rgb(175, 175, 175);
	font-style: italic;
}

.introText {
	line-height: 2rem;
	text-align: center;
	padding-bottom: 4rem;
}

.editing {
	color: rgb(175, 175, 175);
	font-style: italic;
}
