@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
:root {
    --lightBlue: #A6E2F0;
    --midBlue: #7DBBCA;
    --darkBlue: #3D405B;
    --darkestBlue: #0e0f15;
    --green: #81B29A;
    --midGreen: #478466;
    --red: #CC5B3D;
    --midRed: #A03E24;
    --yellow: #F5D118;
    --midYellow: #D4A912;
    --offWhite:  #f2f3f4;

}
h1 {
	font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-weight: 500;
	font-size: 4rem;
	color: var(--darkBlue);
	margin-top: 3rem;
	margin-bottom: 3rem;
	text-align: center;
}

@media screen and (max-width: 768px) {
	h1 {
		line-height: 4rem;
	}
}

h2 {
	font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-weight: 500;
	font-size: 2rem;
	color: var(--darkBlue);
	/* margin-top: 0; */
	/* margin-bottom: 1rem; */
}

h2.footer {
	background-color: var(--midBlue);
	margin: 0;
	margin-bottom: 1rem;
	padding: 2rem;
	text-align: center;
	width: 50%;
}

h2.giftcard {
	padding: 0.5rem;
	padding-bottom: 0;
}

h3 {
	font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-weight: 500;
	font-size: calc(12px + 1.5vw);
	color: var(--darkBlue);
	padding-bottom: 0.5rem;
	margin: 0;
}

input {
    font-family: 'Oswald', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size:calc(12px + 1.5vw); 
    color: rgb(175, 175, 175);
    font-style: italic;
    background-color: var(--offWhite);
    height: 3.5rem;
    overflow: auto;
    width: 100%;
    box-sizing: border-box;
    border: none;
    margin: 0;
    padding-left: 0.5rem;

}

input::-webkit-input-placeholder {
	color: rgb(175, 175, 175);
	font-style: italic;
}

input:-ms-input-placeholder {
	color: rgb(175, 175, 175);
	font-style: italic;
}

input::-ms-input-placeholder {
	color: rgb(175, 175, 175);
	font-style: italic;
}

input::placeholder {
	color: rgb(175, 175, 175);
	font-style: italic;
}

.introText {
	line-height: 2rem;
	text-align: center;
	padding-bottom: 4rem;
}

.editing {
	color: rgb(175, 175, 175);
	font-style: italic;
}

:root {
	background-color: var(--offWhite);
}

section {
	margin-left: 2rem;
	margin-right: 2rem;
}

@media screen and (max-width: 768px) {
	section {
		margin-left: 1rem;
		margin-right: 1rem;
	}
}

.main {
	position: relative;
}

.stickyHeader {
	display: flex;
	z-index: 2;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
}

@media screen and (max-width: 400px) {
	.stickyHeader {
		flex-direction: column;
	}

	.stickyHeader h2.footer {
		width: 100%;
		box-sizing: border-box;
	}
}

button {
    border: none;
    border-radius: 50%;
    height: 2.5rem;
    width: 2.5rem;
    vertical-align: middle;
}

@media screen and (max-width: 768px) {
    button {
        height: 3rem;
        width: 3rem;
    }
}

.editButton {
    background-color: var(--yellow);
    background-image: url(/gift-tracker-app/static/media/pencil.f5a83a80.svg);
    vertical-align: middle;
}

.editButton:hover {
    background-color: var(--midYellow);
}

.editButtonTwo {
    background-color: var(--darkBlue);
    background-image: url(/gift-tracker-app/static/media/pencil.f5a83a80.svg);
    vertical-align: middle;
    fill: var(--green);
    padding-right: 2rem;
}

.editButtonTwo:hover {
    background-color: var(--darkestBlue);
    background-image: url(/gift-tracker-app/static/media/pencil.f5a83a80.svg);
    vertical-align: middle;
}

@media screen and (max-width: 768px) {
    .editButtonTwo {
        margin: auto;
    }
}

.saveButton {
    background-color: var(--yellow);
    background-image: url(/gift-tracker-app/static/media/save.1ea81968.svg);
    vertical-align: middle;
}

.saveButton:hover {
    background-color: var(--midYellow);
}

.deleteButton {
    background-color: var(--red);
    background-image: url(/gift-tracker-app/static/media/minus.3258cc07.svg);
    vertical-align: middle;
}

.deleteButton:hover {
    background-color: var(--midRed);
}

.addButton {
    background-color: var(--green);
    background-image: url(/gift-tracker-app/static/media/plus.adbb1d22.svg);
    vertical-align: middle;
}

.addButton:hover {
    background-color: var(--midGreen);
}

.addButtonTwo {
    background-color: var(--darkBlue);
    background-image: url(/gift-tracker-app/static/media/plus.adbb1d22.svg);
    vertical-align: middle;
}

.addButtonTwo:hover {
    background-color: var(--darkestBlue);
}

.saveButtonTwo {
    background-color: var(--darkBlue);
    background-image: url(/gift-tracker-app/static/media/save.1ea81968.svg);
    vertical-align: middle;
    margin-left: 2rem;
}

.saveButtonTwo:hover {
    background-color: var(--darkestBlue);
}

.tickButton {
    background-color: var(--green);
    background-image: url(/gift-tracker-app/static/media/tick.2e30f8cb.svg);
    vertical-align: middle;
}

.tickButton:hover{
    background-color: var(--midGreen);
}

@media screen and (max-width: 768px) {
    .tickButton {
        margin-left: 1rem;
    }
}

.crossButton {
    background-color: var(--red);
    background-image: url(/gift-tracker-app/static/media/cross.f17e0544.svg);
    vertical-align: middle;
}

@media screen and (max-width: 768px) {
    .crossButton {
        margin-left: 1rem;
    }
}

.crossButton:hover {
    background-color: var(--midRed);
}

.buttonCentre {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.arrow {
    background-image: url(/gift-tracker-app/static/media/downArrow2.57cabded.svg);
    height: 2rem;
    width: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    
}
.table {
    background-color: var(--lightBlue);
    padding: 2rem;

}

/* @media screen and (max-width: 768px) {
    .table {
        padding: 1rem;
        width: 80%;
    }
} */

td {
    height: 6rem;
    vertical-align: middle;
}

.tableInputs {
    background-color: var(--offWhite);
    border-top: 2px solid var(--lightBlue);
    border-bottom: 2px solid var(--lightBlue);
    text-align: center;
    vertical-align: middle;
    height: 3rem;
    width: 30%;
}

td.tableInputs:last-of-type {
    border-top: 2px solid var(--lightBlue);
    border-bottom: 2px solid var(--lightBlue);
}

.tableTextInput {
    background-color: var(--offWhite);
    border: none;
    text-align: center;
    height: 4rem;
    width: 100%;
    display: block;
    padding: 0;
    margin: 0;
}

.tdNarrow {
    max-width: 4rem;
    padding-right: 1rem;
}

.tableButtons {
    display: block;
    margin: auto;
}

.tableRow {
    border-left: 2rem solid var(--lightBlue);
    border-right: 2rem solid var(--lightBlue);
    /* width: 100%; */
}

.tableRow:last-of-type {
    border-bottom: 2rem solid var(--lightBlue);
}

th {
    background-color: var(--lightBlue);
}

table {
    background-color: var(--lightBlue);
    border-spacing: 0;
    width: 100%;
}



.cardHolder {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

}

@media screen and (max-width: 768px) {
    .cardHolder {
        flex-direction: column;
    }
}

.card {
    width: 50%;
}
/* 
@media screen and (max-width: 768px) {
    .card {
        margin: auto;
    }

} */

.budgetHolder {
    background-color: var(--green);
    display: flex;
    flex-direction: row;
    width: 50%;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 1rem;
    padding-top: 1rem;
    margin: 0;
    margin-bottom: 1rem; /* Keep this as-is or desktop shifts */
}

@media screen and (max-width: 768px) {
    .budgetHolder {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
    }
}

.budgetCard {
    margin: 0;
    
}

@media screen and (max-width: 768px) {
    .budgetCard {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }
}

.friendHolder {
    background-color: var(--yellow);
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 4rem;
    justify-content: center;
    align-items: center;
    padding-bottom: 1rem;
    padding-top: 1rem;
    padding-left: 15rem;
    padding-right: 15rem;
    margin-bottom: 1rem; /* Keep this as-is or desktop shifts */

}

@media screen and (max-width: 768px) {
    .friendHolder {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
    }
}
